  text-align: center;
  color: gold;
  font-size:50px;
  padding:20px;
}
.full > a:hover{
  color: navy;
  -webkit-transition: color 2s;
  transition: color 2s;
}
.aboutme {
  grid-area: 1 / 1 / 4 / 1;
}
.work {
  grid-area: 2 / 2 / 4 / 4;
}
.welcome {
  grid-area: 1 / 4 / 5 / 6;
  background-image: url(/portfolio/static/media/rose-egg.d5dc20f0.svg);
  background-size: contain;
  background-position: right;
  /* background-attachment: fixed; */
  background-repeat: no-repeat;
  overflow: visible;
  display: grid;
  place-items: center;
}
