  color:navy;
  font-size:4em;
  text-align: right;
  border-right: 5px dotted navy;
  padding-right:20px;
  margin:20px;
}
.welcome p:hover{
  color: magenta;
  -webkit-transition: color 2s;
  transition: color 2s;
}
.logo{
  display: none;
}
.iconsnav {
  grid-area: 4 / 1 / 4 / 4;
}
.iconsnav a{
