}
.iconsnav img:hover {
  -webkit-filter: invert(15%) sepia(91%) saturate(6288%) hue-rotate(215deg) brightness(56%) contrast(112%);
          filter: invert(15%) sepia(91%) saturate(6288%) hue-rotate(215deg) brightness(56%) contrast(112%);
