@media screen and (max-width: 768px) {
.full{
  height: auto;
    display: flex;
    flex-wrap: wrap;
    margin:0;
    padding:0;
    padding-top:30%;
    background-image: url("../images/rose-egg.svg");
    background-position: top;
    background-size: cover;
  }
  .full > a{
    color:navy;
    font-size:4em;
    width: 100%;
    margin:0;
  }
  .welcome{
    display:none;

}
.logo{
    background-color:deeppink;
}

.iconsnav{
  width:95vw;
}
.iconsnav, .navTop{
   position:fixed;
   top:0;
    margin:0;
    z-index:1;
}
.iconsnav img{
    width: 15%;
    height: 15%;
  filter: invert(12%) sepia(97%) saturate(7491%) hue-rotate(217deg) brightness(57%) contrast(129%);

  }
.iconsnav a:last-child{
  font-size: 17vw;
  color:#1f489f;
  }
.navTop{
  background-color: tan;
  width:100%;
  padding: 5px;
  justify-content: space-evenly;
}
.navTop a{
  float:right;
  color:#000080;
}
.navTop img{
  font-size: 15px;
  margin:0;
  filter:none;
  filter: invert(12%) sepia(97%) saturate(7491%) hue-rotate(217deg) brightness(57%) contrast(129%);

}
.back-arrow{
  color:navy;

}
.aboutbox-article{
  display: block;
  margin:0;
}
.aboutbox p{
  padding: 10px;
  margin: 0;
}
.date{
  display: none;
}
.projectbox:not(:first-of-type){
  margin-top:5px;
}
.projectbox ul{
  position: static;
  opacity:1;
  transform: none;
  }
  .projectbox li:last-child {
    width: auto;
    padding:0;

  }
  .button i{
    font-size: 3em;
  }
  .collab{
    display:none;
  }
  .contact{
    display:none;
  }
  }