.home{
  /* overflow: hidden; */
  width: 100%; 
  font-family: 'Cabin Sketch', cursive;
}
.full {
  height: 100vh;
  display: grid;
  grid-template-rows: repeat (5, 1fr);
  grid-template-columns: auto-fit 1fr 1fr 1fr 600px;
  grid-gap: 15px;
  padding: 0 0 20px 20px;

}
.full > a,
.iconsnav:not(.welcome) {
  /* border-left: 4px black solid;
  border-top: 4px black solid; */
  align-self:center;
  text-align: center;
  color: gold;
  font-size:50px;
  padding:20px;
}
.full > a:hover{
  color: navy;
  transition: color 2s;
}
.aboutme {
  grid-area: 1 / 1 / 4 / 1;
}
.work {
  grid-area: 2 / 2 / 4 / 4;
}
.welcome {
  grid-area: 1 / 4 / 5 / 6;
  background-image: url(../images/rose-egg.svg);
  background-size: contain;
  background-position: right;
  /* background-attachment: fixed; */
  background-repeat: no-repeat;
  overflow: visible;
  display: grid;
  place-items: center;
}
.welcome p {
  font-family: 'Cabin Sketch', cursive;
  color:navy;
  font-size:4em;
  text-align: right;
  border-right: 5px dotted navy;
  padding-right:20px;
  margin:20px;
}
.welcome p:hover{
  color: magenta;
  transition: color 2s;
}
.logo{
  display: none;
}
.iconsnav {
  grid-area: 4 / 1 / 4 / 4;
}
.iconsnav a{
  padding: 0 10px 0 10px;
}
.iconsnav img{
  height: 3em;
}
.iconsnav a:last-child{
  /* color:#FFD700; */
  font-size: 1.5em;
  padding:0;
}
.iconsnav a:last-child:hover{
  color: #000080;
}
.iconsnav img:hover {
  filter: invert(15%) sepia(91%) saturate(6288%) hue-rotate(215deg) brightness(56%) contrast(112%);
}